body{
    background-image: url(/Zest-Frontend/static/media/recipe-background-image.01705941.jpeg);
    box-sizing: border-box;
}

.Home{
    align-items: center;
    justify-content: center;
}

.Home h1{
    color: #666699;
    font-family: "Noto Sans Old Italic";
}

.content{
    max-width: 800px;
    margin: 40px auto;
}

.content h1{
    text-align: center;
}

.recipe-preview {
    display: flex;
    flex-direction: column;
    padding: 10px 16px;
    margin: 20px 0;
    border: 1px solid #f1356d;
    background-color: white;

    justify-content: space-between;
}
.recipe-preview:hover {
    box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.recipe-preview h2 {
    font-size: 40px;
    color: #f1356d;
    margin-bottom: 8px;
}

.recipe-preview p{
    color: #666699;
    font-size: 17px;
    font-family: "Noto Sans Old Italic";
}
.recipe-preview a{
    text-decoration: none;
    cursor: pointer;
}

.recipe-preview-description{
    margin: 5px;
    padding: 10px;
    justify-self: center;
}


.recipe-preview img{
    align-self: flex-end;
    justify-self: flex-end;
}

.recipe-preview-rating{
    display: flex;
    justify-self: flex-end;
    align-self: center;
}

.recipe-header{
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.recipe-content{
    display: flex;

}

.home-input{
    border: 1px solid #f1356d;
    outline: none;
    padding: 5px;
    border-radius: 10px;
}

.editable-preview{
    display: flex;
    padding: 10px 16px;
    margin: 20px 0;
    border: 1px solid #f1356d;
    background-color: white;
    justify-content: space-between;
}

.editable-preview h2{
    font-size: 40px;
    color: #f1356d;
    margin-bottom: 8px;
}

.category-selection{
    color: #ffffff;
    background-color: var(--primary-color);
    border-color: #f1356d;
    outline: none;

}

.loading{
    position: absolute;
    top: 50%;
    right: 50%;
}


@media (max-width: 767px) {
    .recipe-content{
        flex-direction: column;
    }
    .recipe-preview img{
        margin: 1rem;
        width: 90%;
        height: 80%;
        align-self: center;
        justify-self: center;
    }

    .recipe-preview{
        margin-left: 1rem;
        margin-right: 1rem;
    }

    .editable-preview{
        margin-left: 1rem;
        margin-right: 1rem;
    }
}


.navbar {
    background-color: #f1356d;
    color: #fff;
    margin-top: 0;
    height: 50px;

}

.navbar ul {
    display: flex;

}

.navbar a {
    color: #fff;
    padding: 10px;
    margin: 0 5px;
}

.navlink{
    color: #fff;
    padding: 10px;
    margin: 0 5px;
}

.navlink:hover{
    border-bottom: 2px #fff solid;
}

.navbar a:hover {
    border-bottom: 2px #fff solid;
}

.navbar .flex {
    justify-content: space-between;

}

.lang-selection{
    color: white;
    background-color: var(--primary-color);
    border: none;
    outline: none;
}



@media (min-width: 768px) {
    .bottombar-header{
        display: none;
    }
}

@media (max-width: 767px) {


    .navbar{
        z-index: 100;
        height: 50px;
        overflow: hidden;
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        overflow: hidden;
    }

    .navbar ul{
        display: none;
    }

    .navbar h1{
        justify-self: center;
        align-self: center;
    }



    .bottombar-header{
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 100;
    }

    .bottombar{
        max-width: 968px;
        display: flex;
        align-self: center;
        justify-self: center;

        justify-content: space-between;
        align-items: center;
        min-width: 400px;
        width: auto;


    }

    .nav__toggle{
        cursor: pointer;
        font-size: 1.1rem;
        margin-left: 2rem;
        font-size: 2rem;

    }
    .nav__toggle:hover{
        color: var(--secondary-color);
    }

    .nav-menu{
        width: 80%;
        margin-right: 2rem;
        padding: 2rem 1.5rem 1rem 1rem;
        box-shadow: 0 -1px 4px rgba(0,0,0,.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;
    }
}

.bottombar ul{
    grid-template-columns: repeat(3, 1fr);
}

.bottombar ul .bottom-link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: x-large;

}
:root {
    --primary-color: #f1356d;
    --secondary-color: #666699;
    --dark-color: #002240;
    --light-color: #f4f4f4;
    --success-color: #5cb85c;
    --error-color: #d9534f;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    font-family: 'Lato', sans-serif;
    color: #333;
    line-height: 1.6;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: #333;
}

h1,
h2 {
    font-weight: 300;
    line-height: 1.2;
    margin: 10px 0;
}

/*p {*/
/*    margin: 10px 0;*/
/*}*/



code,
pre {
    background: #333;
    color: #fff;
    padding: 10px;
}

.hidden {
    visibility: hidden;
    height: 0;
}

.container {
    max-width: 1100px;
    margin: 0 auto;
    overflow: auto;
    padding: 0 40px;
}

.card {
    background-color: #fff;
    color: #333;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
    padding: 20px;
    margin: 10px;
}

.btn {
    display: inline-block;
    padding: 10px 30px;
    cursor: pointer;
    background: #f1356d;
    background: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 5px;
}

.btn-outline {
    background-color: transparent;
    border: 1px #fff solid;
}

.btn:hover {
    transform: scale(0.98);
}

/* Backgrounds & colored buttons */
.bg-primary,
.btn-primary {
    background-color: #f1356d;
    background-color: var(--primary-color);
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
    margin: 5px;
}

.bg-secondary,
.btn-secondary {
    background-color: #666699;
    background-color: var(--secondary-color);
    color: #fff;
}

.bg-dark,
.btn-dark {
    background-color: #002240;
    background-color: var(--dark-color);
    color: #fff;
}

.bg-light,
.btn-light {
    background-color: #f4f4f4;
    background-color: var(--light-color);
    color: #333;
}

.bg-primary a,
.btn-primary a,
.bg-secondary a,
.btn-secondary a,
.bg-dark a,
.btn-dark a {
    color: #fff;
}

/* Text colors */
.text-primary {
    color: #f1356d;
    color: var(--primary-color);
}

.text-secondary {
    color: #666699;
    color: var(--secondary-color);
}

.text-dark {
    color: #002240;
    color: var(--dark-color);
}

.text-light {
    color: #f4f4f4;
    color: var(--light-color);
}

/* Text sizes */
.lead {
    font-size: 20px;
}

.sm {
    font-size: 1rem;
}

.md {
    font-size: 2rem;
}

.lg {
    font-size: 3rem;
}

.xl {
    font-size: 4rem;
}

.text-center {
    text-align: center;
}

/* Alert */
.alert {
    background-color: #f4f4f4;
    background-color: var(--light-color);
    padding: 10px 20px;
    font-weight: bold;
    margin: 15px 0;
}

.alert i {
    margin-right: 10px;
}

.alert-success {
    background-color: #5cb85c;
    background-color: var(--success-color);
    color: #fff;
}

.alert-error {
    background-color: #d9534f;
    background-color: var(--error-color);
    color: #fff;
}

.flex {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.grid {
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem;

}

.grid-3 {
    grid-template-columns: repeat(3, 1fr);
}

/* Margin */
.my-1 {
    margin: 1rem 0;
}

.my-2 {
    margin: 1.5rem 0;
}

.my-3 {
    margin: 2rem 0;
}

.my-4 {
    margin: 3rem 0;
}

.my-5 {
    margin: 4rem 0;
}

.m-1 {
    margin: 1rem;
}

.m-2 {
    margin: 1.5rem;
}

.m-3 {
    margin: 2rem;
}

.m-4 {
    margin: 3rem;
}

.m-5 {
    margin: 4rem;
}

/* Padding */
.py-1 {
    padding: 1rem 0;
}

.py-2 {
    padding: 1.5rem 0;
}

.py-3 {
    padding: 2rem 0;
}

.py-4 {
    padding: 3rem 0;
}

.py-5 {
    padding: 4rem 0;
}

.p-1 {
    padding: 1rem;
}

.p-2 {
    padding: 1.5rem;
}

.p-3 {
    padding: 2rem;
}

.p-4 {
    padding: 3rem;
}

.p-5 {
    padding: 4rem;
}


.recipe-details h2 {
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 10px;
}
.recipe-details{
    margin: 20px 0;
}
.recipe-details button {
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}

.recipe-details button:focus{
    background-color: #666699;
}

.recipe-detail-container{
    display: flex;
    background-color: white;
}



.recipe-detail-appeareance{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}

.recipe-information{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;

}

.recipe-information button{
    justify-self: flex-end;
}

.recipe-information h2{
    font-size: 30px;
    width: auto;
}

.recipe-owner{
    font-family: "Noto Sans Old Italic";
    font-size: 1.3rem;
}

.recipe-owner a{
    color: var(--secondary-color);
}

.recipe-category{
    color: #f1356d;
    font-family: "Noto Sans Old Italic";
    font-size: 1.3rem;
}

.recipe-detail-ingredients{
    display: flex;
    flex-direction: column;
    justify-self: center;
    align-self: center;
    padding: 10px;
}

.recipe-detail-ingredients .one-ingredient{
    justify-self: center;
    align-self: center;
}

.recipe-detail-description{
    padding: 20px;
}

.tag-link{
    color: var(--primary-color);

}


.create {
    min-width: 400px;
    width: auto;
    margin: 0 auto;
    text-align: center;
}

.drop-img-zone{

}

.create label {
    text-align: left;
    display: block;
    font-size: 15px;
}
.author{
    margin-bottom: 15px;
}
.create h2 {
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 30px;
    font-weight: bold;
}
.create input, .create textarea, .create select {
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
    outline: none;
}

.create h3{
    text-align: left;
    color: #f1356d;
}

.create .add-ingredient input{
    display: inline-block;
}

.create .ingredients{
    padding: 20px;
    margin: 10px;
}

.ingredient span{
    background-color: #f1356d;
    color: #ffffff;
    padding: 2px;
    border-radius: 8px;
    cursor: pointer;
}
.ingredient p{
    font-size: 20px;
    display: inline-block;
    margin: 5px;
}

.updateRecipe-list-button-container{
    display: flex;
}

@media (max-width: 767px) {
    .create h2{
        margin-top: 2rem;
        font-size: 2rem;
    }
    .create label{
        margin-left: 1rem;
    }

    .create input, .create textarea, .create select {
        width: 100%;
        padding: 6px 10px;
        margin: 10px;
        border: 1px solid #ddd;
        box-sizing: border-box;
        display: block;
        outline: none;
    }
}



.profile-container{
    height: auto;
    background-color: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border: 3px solid #61dafb;
    border-radius: 10px;
    width: auto;
    padding: 5px;
    margin: auto;
}

.profile-item-container{
    display: flex;
    flex-direction: row;

}

.relation-description-container{
    display: flex;
    flex-direction: column;
    width: auto;
}

.profile-relations{
    display: inline-block;
    padding: 10px;
    width: auto;
    height: auto;
}

.profile-features{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 35%;


}

.profile-img{
    height: auto;
    width: 150px;
    padding-left: 10px;
    border-radius: 50%;
}

.myName{
    display: flex;
    padding: 10px;
    font-size: 20px;
    color: #f1356d;

}

.myFollowers, .myFollowings{
    display: inline-block;
    margin: 15px;
    padding: 5px;
}

.user-description-container{
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.user-description{
    display: flex;
    border: 1px solid #f1356d;
    border-radius: 15px;
    padding: 10px;
    outline: none;
}

.user-description-button{
    width: 30%;
    align-self: center;
}

.user-description-button{
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
    margin: 5px;
}

.user-description-button:focus{
    background-color: #666699;
}


/* DROPDOWN
============================================ */

.dd-wrapper {
    display: flex;
    min-height: 15px;
    width: 50px;
    flex-wrap: wrap;

}

.dd-header {
    background-color: white;
    border-color: #ccc;
    border-radius: 4px;

    border-width: 1px;

    display: flex;
    justify-content: space-between;
    cursor: pointer;
    width: 100%;
    padding: 0 20px;
    font-weight: bold;
}

.dropdown-container{
    display: flex;
    position: absolute;
    flex-direction: column;
    margin-top: 20px;
    height: 200px;
    min-width: 200px;
    border: 1px solid #f1356d;
    background-color: #ffffff;
    border-radius: 15px;

}

.dd-list {
    display: flex;
    padding: 0;
    margin-left: 15px;
    width: 100%;
    margin-top: 20px;
    overflow-y: scroll;
    flex-direction: column;
}

dd-list-item-container{
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    padding: 5px;
    border: 1px solid #dddddd;
    cursor: pointer;
}

.dropdown-search{

    margin-left: 15px;
    border: 1px solid #f1356d;
    outline: none;
    padding: 1px;
    border-radius: 10px;
    width: 100px;
    align-self: flex-end;
}

.dd-link {
    cursor: pointer;
    align-self: flex-start;
}

.dd-button{
    background: #f1356d;
    color: #fff;
    border: 0;
    border-radius: 8px;
    cursor: pointer;
    align-self: flex-end;
}

.dd-button:hover{
    background-color: #666699;
    cursor: pointer;
}




.discover-content{
    flex-direction: column;
    max-width: 800px;
}

.discover-recipe-list{
    display: flex;
    flex-wrap: wrap;
    min-width: 490px;
    width: auto;
}

.recipe-preview-discover{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    margin: 10px;
    width: 30%;
    background-color: white;
}

.discover-recipe-features{
    display: flex;
    justify-content: space-between;

}

.discover-recipe-features h2{
    padding: 5px;
    color: #f1356d;
}

.discover-recipe-rating{
    margin-right: 7px;
    padding-bottom: 10px;
    justify-self: flex-end;
    align-items: center;
}

.discover-recipe-owner{
    padding: 5px;

}

.recipe-preview-discover:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.recipe-preview-discover img{
    width: 100%;
}

@media (max-width: 767px) {
    .recipe-preview-discover{
        width: 30%;
        padding: 5px;
        margin: 5px;
    }
    .discover-recipe-features{
        display: none;
    }

    .discover-content h1{
        margin-top: 1rem;
        font-size: 2rem;
    }
}
